import { motion, AnimatePresence, AnimateSharedLayout } from 'framer-motion'
import React, { useState, useEffect, useRef, useContext } from 'react'
import Observer from '../../../utils/Observer'
import cn from 'classnames'
import './style.scss'
import sanityClient from '../../../client.js'
import Spinner from '../../../components/Spinner'
import { Lang } from '../../../components/Context'
import { Arrow } from '../../../components/Arrow'

const transition = { duration: .7, ease: [0.43, 0.13, 0.23, 0.96] }

const Carousel = () => {
  
    const items = [
      {
        "type": "img",
        "area": [10, 4, 50, 8],
        "src": 'https://cdn.sanity.io/images/r9dm8hy5/production/2a38e6c6015857e228c6b1193e9a29a0b798fcb9-2757x3493.webp?w=1000&h=1000&fit=max'
      },
      {
        "type": "video",
        "area": [20, 13, 75, 15],
        "src": 'v1.mp4'
      },
      {
        "type": "caption",
        "area": [70, 2, 75, 10],
        "body": ['In the ear of Gogh', '72x60', 'Master Project']
      },
      //
      {
        "type": "img",
        "area": [35, 32, 60, 10],
        "src": 'https://cdn.sanity.io/images/r9dm8hy5/production/b0336fe5e62bb59af7030edb8d24bb2c04ba3626-2555x2632.webp?w=1000&h=1000&fit=max'
      },
      {
        "type": "caption",
        "area": [20, 30, 20, 20],
        "body": ['Dans les pensées du penseur', '48x48', 'Master Project']
      },
      //
      {
        "type": "img",
        "area": [67, 57, 30, 10],
        "src": 'img/home/IMG_0158'
      },
      {
        "type": "img",
        "area": [10, 45, 50, 20],
        "src": 'img/home/01_AMOR_EN_LA_FRONTERA_low-res 72x 148 copie'
      },
      {
        "type": "img",
        "area": [5, 70, 22, 10],
        "src": 'img/home/Le coeur mid res'
      },
      {
        "type": "video",
        "area": [51, 65, 38, 17],
        "src": 'IMG_2469 copie.MOV'
      },
      {
        "type": "caption",
        "area": [34, 66, 75, 10],
        "body": ['AMOR EN LA FRONTERA', '72x148', 'Série Émergence Créative']
      },
      //
      {
        "type": "img",
        "area": [10, 83, 75, 10],
        "src": "https://cdn.sanity.io/images/r9dm8hy5/production/b5d327715170d0003e82440460a1c7d2c108db76-1024x1010.webp?w=1000&h=1000&fit=max"
        
      },
      {
        "type": "video",
        "area": [20, 93, 75, 10],
        "src": "andy.mp4"
      },
      {
        "type": "caption",
        "area": [80, 90, 10, 10],
        "body": ['Andy', '72x72', 'Master Project']
      },
      {
        "type": "img",
        "area": [24, 113, 75, 10],
        "src": "https://cdn.sanity.io/images/r9dm8hy5/production/e03fb5d04257cb4e18ad88a88dd1f5e6c9517507-1000x1204.webp?w=1000&h=1000&fit=max"
        
      },
      {
        "type": "video",
        "area": [5, 103, 75, 10],
        "src": "The roots Video.mp4"
      },
      {
        "type": "caption",
        "area": [7, 119, 10, 10],
        "body": ['The Roots', '72x60', 'Série Émergence Créative']
      },
      //
      {
        "type": "img",
        "area": [30, 136, 75, 10],
        "src": "img/home/dead end"
        
      },
      {
        "type": "video",
        "area": [20, 126, 75, 10],
        "src": "52BDDBB6-BCB8-4BD4-9811-A3FF5F263F34.MOV"
      },
      {
        "type": "video",
        "area": [8, 146, 75, 10],
        "src": "DAF7CC98-B054-49C6-889D-F7FD5E8F9ECE.MOV"
      },
      {
        "type": "caption",
        "area": [80, 136, 10, 8],
        "body": ['Dead End', '?', '?']
      },
    ] 

    
    const [data, setData] = useState(null)
    useEffect(() => {
        sanityClient
        .fetch(`*[_type == "carousel"]{
            _id,
            title,
            dimensions,
            serie->{title},
            'order': order,
            images[]{
              _key,
              asset->{
                  _id,
                  'url': url + '?format=auto&fm=webp',
                  'w': metadata.dimensions.width,
                  'h': metadata.dimensions.height
              }
            },
            videos[]{
              _key,
              asset->{
                  _id,
                  url,
              }
          },
          } | order(order asc)`)
        .then((data) => {setData(data)})
        .catch(console.error)
    }, [])

    const [[index, direction], setIndex] = useState([0,0]);



    


    function paginate(x) {

      let newIndex = index + x;
      
      if (newIndex < 0) newIndex = data.length - 1;
      if (newIndex > data.length - 1) newIndex = 0;
      setIndex([newIndex, x])

    }


        return data? (
          <div className='carousel' 
          
          >
            <div className='dots'>
              {data.map((slide, i) => (

                <motion.div 
                
                animate={i===index?{opacity: 1, scale: 1.2}:{opacity: 0.4}}
                whileTap={{scale:0.9}}
                onClick={() => setIndex(([oldIndex, direction]) => {
                
                  console.log(oldIndex < i? -1:1)
                  return ([i, oldIndex > i? -1:1])
                })} 
                className={`dot ${i===index?'active':''}`}>

                </motion.div>

              ))}
            </div>

            
                <motion.div className='prev' whileTap={{scale: 0.9 }} onClick={() => paginate(-1)}>
                  <Arrow deg={180} />
                </motion.div>
                <motion.div className='next' whileTap={{scale: 0.9 }} onClick={() => paginate(1)}>
                   <Arrow />
              </motion.div>
            
                <motion.div 
              //animate={{x: -100*index}}
              
              transition={transition}
               className='background'></motion.div>
              
            <AnimatePresence initial={false} custom={direction}>

            {data.map((slide, i) => i === index && (

              <Slide paginate={paginate} key={slide._id} slide={slide} direction={direction} />
              
            ))}
            </AnimatePresence>

          </div>
          
        ) : (<Spinner />)
}

const Slide = ({slide, direction, paginate}) => {
  const lang = useContext(Lang);


  const [format, setFormat] = useState()

  function resize() {
    setFormat(slide.images[0].asset.w > slide.images[0].asset.h*1.5 || window.innerWidth < 1100 ? 'horizontal':'vertical')
  }

  useEffect(() => {
    resize()
    window.addEventListener('resize', resize)
    return () =>  window.removeEventListener('resize', resize)
  }, [])
  
  const variants = {
  
    enter: direction => ({
        x: direction > 0 ? window.innerWidth : -window.innerWidth,
        opacity: 0
    }),
    center: {
      zIndex: 1,
      x: 0,
      opacity: 1
    },
    exit: direction => ({
      
        zIndex: 0,
        x: direction < 0 ? window.innerWidth : -window.innerWidth,
        opacity: 0
      
    })
  };

  const [ready, setReady] = useState(true);

  const [selected, setSelected] = useState();
  const [section, setSection] = useState();

  const swipeConfidenceThreshold = 10000;
const swipePower = (offset, velocity) => {
  return Math.abs(offset) * velocity;
};

  return (
    <motion.div 

              custom={direction}
              variants={variants}
              initial="enter"
              animate="center"
              exit="exit"
              transition={{ duration: 1.4, ease: [0.43, 0.13, 0.23, 0.96] }}
              //onAnimationStart={() => setReady(false)}
              //onAnimationComplete={() => setReady(true)}
              className={`slide ${format}`}
              drag="x"
              dragConstraints={{ left: 0, right: 0 }}
              dragElastic={1}
              onDragEnd={(e, { offset, velocity }) => {
                const swipe = swipePower(offset.x, velocity.x);

                if (swipe < -swipeConfidenceThreshold) {
                  paginate(1)
                } else if (swipe > swipeConfidenceThreshold) {
                  paginate(-1)
                }
              }}
              
              >

                      <div className='main'>
                        
                      
                          
                          <motion.div className='img' initial={{opacity: 0}} animate={{opacity: 1}} transition={transition} >
                            <motion.img key={slide} initial={{scale: 2}} animate={{scale: 1}} src={slide.images[0].asset.url} />
                          </motion.div>
                          <motion.div 
                          transition={{delay: 0, ...transition}} 
                          animate={{opacity:1}} 
                          initial={{opacity:0}}
                          
                          className='text'>
                            <h4 className='title'>{slide.title}</h4>
                            <h5 className='dims'>{slide.dimensions + (lang === 'fr'?' po':' in')}</h5>
                            <h5 className='serie'>{slide.serie.title[lang]}</h5>
                          </motion.div>
                        
                          
                      </div>
                       
                      {<div className={cn('content', {selected: section === 'imgs'})}>
                      

                          {slide.images && slide.images.length > 1 && <div className={cn('imgs', {selected: section === 'imgs'})}>
                          {slide.images.map((img, index) => index!==0 && (
                            
                              <motion.div transition={{delay: .1+.1*index , ...transition}} 
                              animate={{opacity:1}} 
                              onClick={() => {
                                if (selected !== img._key) {
                                  setSelected(img._key); 
                                  setSection('imgs');
                                } else {
                                  setSelected(null); 
                                  setSection(null);
                                }
                                }}
                                
                              initial={{opacity:0}} 
                              className={cn('img', {selected: img._key === selected})}>
                                <img src={img.asset.url} />
                              </motion.div>
                              
                            
                          ))}
                          </div>}
                          
                        
                        
                      
                      

                        {slide.videos && <div className={cn('videos', {selected: section === 'videos'})}>
                          {slide.videos.map((video, index) => (
                            <motion.div
                            
                            onClick={() => {
                                if (selected !== video._key) {
                                  setSelected(video._key); 
                                  setSection('video');
                                } else {
                                  setSelected(null); 
                                  setSection(null);
                                }
                                }}
                            exit={{opacity:0}}
                            animate={ready? {opacity:1, y: 0}:{opacity:0}} 
                            initial={{opacity:0}} 
                            transition={{delay: .4 + .4*index , ...transition}}
                            className={cn('video', {selected: video._key === selected})}>
                              <video autoPlay muted loop src={video.asset.url} />
                            </motion.div>
                          ))}
                        </div>}
                        </div>}
                
                   
                   
                    
                  
              </motion.div>
  )
}




export default Carousel
