import { useState, useEffect, useRef, useContext } from 'react'
import { motion, AnimatePresence, AnimateSharedLayout  } from "framer-motion";

import Carousel from './Carousel/index'

import './style.scss'

import Spinner from '../../components/Spinner';
import Nouvelles from '../nouvelles';
import { Lang } from '../../components/Context';
import { Link } from 'react-router-dom';
import { Arrow } from '../../components/Arrow';


const Home = ({data, nouvelles, pageReady}) => {
    
    const transition = { duration: .5, ease: [0.43, 0.13, 0.23, 0.96] };
    
    const lang = useContext(Lang)

    const [scrollMore, setScrollMore] = useState(false);

    function scroll() {
        if (window.scrollY < 10) setScrollMore(true);
        else setScrollMore(false);
    }

    useEffect(() => {
        scroll()
        window.addEventListener('scroll', scroll);
        return () => window.removeEventListener('scroll', scroll);
    }, [])
    

    return (
        <motion.div className='home' 
       initial={{y: 100, opacity: 0}}
       animate={{y:0, opacity: 1}}
       exit={{y:-100, opacity: 0}}
       
        transition={transition}>
  
                        <div className='main-video'>
                        <video autoPlay loop muted playsInline src={`/videos/${lang}.mp4`} />
                        </div>
                        <AnimatePresence>
                        {scrollMore && (
                        <motion.div 
                        
                        transition={transition}
                        initial={{y: 100, opacity: 0, transition: {delay: .5}}}
                        animate={{y:0, opacity: 1}}
                        exit={{y:-100, opacity: 0}}
                        className='scroll-more'>
                            
                            <div className='text'>{lang==='fr'?<h4>Défilez pour en voir plus</h4>:<h4>Scroll for more</h4>}</div>
                            <Arrow deg={90} />
                        </motion.div>
                        )}
                        </AnimatePresence>
                

                        <Carousel />

                        {/*<p className='p1'>Depuis toujours, je suis fasciné par le corps humain. Pratiquement toute ma production artistique est orientée vers cette thématique. Après mes études collégiales, j’ai pensé devenir illustrateur médical. Je voyais cette avenue comme une façon de conjuguer ma passion de l’art et des sciences. Sauf que je n’avais pas le cœur assez solide. L’idée de manipuler des corps inertes me répugnait!</p>
                        <p className='p2'>
                        De ce fait, j’ai réussi à manier, à ma façon, le corps humain. Je me considère comme un plasticien qui intègre à ses œuvres des personnages hypertexturés représentant la chair, formant ainsi l’expression du corps et exprimant ses émotions, sa sensibilité. Ces personnages sont dans des univers abstraits, parfois femmes, parfois hommes. Je ne ressens pas le besoin de les définir. Il s'agit tout simplement d'êtres humains, de personnes autour desquelles le spectateur peut créer son propre scénario.
                        </p>
                        <p className='p3'>
                        Lorsque je peins, il m’est impossible d’analyser ma méthode de travail. C’est instinctif. Je désire que le public puisse interpréter à sa façon. Je veux lui laisser un « flou » pour qu’il découvre et ressente par lui-même. Je tente de suggérer et non de montrer. Je laisse le spectateur dans un univers de suggestions à travers les formes, les couleurs et les textures. J’apprécie toujours que les gens me disent ce qu’ils voient, ce qu'ils perçoivent. Pour moi, c’est partie intégrante de l’œuvre. Dans ce contexte, je me dois d’être flexible.
                    </p><p className='p4'>
                        Lorsque je peins, il m’est impossible d’analyser ma méthode de travail. C’est instinctif. Je désire que le public puisse interpréter à sa façon. Je veux lui laisser un « flou » pour qu’il découvre et ressente par lui-même. Je tente de suggérer et non de montrer. Je laisse le spectateur dans un univers de suggestions à travers les formes, les couleurs et les textures. J’apprécie toujours que les gens me disent ce qu’ils voient, ce qu'ils perçoivent. Pour moi, c’est partie intégrante de l’œuvre. Dans ce contexte, je me dois d’être flexible.
                    </p>
                        
                        */}


                        {lang==='fr'? 
                        <p>
                        Internationalement connues, les œuvres de Tremblay sont exposées à la fois au <strong>Canada, aux États-Unis, en Europe, en Asie</strong> ainsi qu’au <strong>Moyen-Orient</strong> et ce, dans <strong>plusieurs illustres galerie</strong>s. Tremblay a été un des trois Canadiens à représenter le Canada pour le <strong>G20 en Chine</strong>. Basé à <strong>Dubaï</strong> de 2012 à 2016 où il a enseigné l’histoire de l’art moderne à l’Université canadienne, Sylvain Tremblay s’est ensuite dirigé vers un nouvel atelier à <strong>New York</strong> où il a obtenu le <strong>O-1 Visa</strong>. En novembre 2019, Tremblay a été sélectionné pour présenter une performance en direct lors de l’inauguration du Pavillon du Canada dans le cadre de <strong>l’Exposition universelle de 2020 à Dubaï</strong>. En mai dernier, une œuvre de Tremblay a été choisie par l’<strong>UNESCO</strong> pour créer un masque et participer à l’exposition Mask Art. Cette année, Tremblay a été nommé comme professeur à vie à l’<strong>Université Canadienne de Dubaï</strong> où il a un atelier depuis 2012. En août dernier, Tremblay a représenté fièrement le <strong>Canada au Japon</strong> dans le cadre d’une exposition au <strong>Chiba City Museum of Art</strong> lors du <strong>20th Japan International Art Exchange Exhibition</strong>.
                        </p>
                        :
                        <p>
                        Internationally known, Tremblay's works are exhibited in <strong>Canada</strong>, the <strong>United States</strong>, <strong>Europe</strong>, <strong>Asia</strong> and the <strong>Middle East</strong> in <strong>several famous galleries</strong>. Tremblay was one of three Canadians to represent Canada for the <strong>G20 in China</strong>. Based in <strong>Dubai</strong> from 2012 to 2016 where he taught modern art history at the Canadian University, Sylvain Tremblay then headed to a new studio in New York where he obtained the <strong>O-1 Visa</strong>. In November 2019, Tremblay was selected to present a live performance at the opening of the Canada Pavilion at the <strong>2020 World Expo in Dubai</strong>. Last May, a work by Tremblay was chosen by <strong>UNESCO</strong> to create a mask and participate in the Mask Art exhibition. This year, Tremblay was appointed as a lifetime professor at the <strong>Canadian University of Dubai</strong> where he has had a studio since 2012. Last August, Tremblay proudly represented <strong>Canada in Japan</strong> in an exhibition at the <strong>Chiba City Museum of Art for the 20th Japan International Art Exchange Exhibition</strong>. 
                        </p>
                        }
                    
                        
                        <div className='nouvelles-home'>

                        <div className='nouvelles-titre'>
                            
                            <h3>{lang==='fr'?'Nouvelles récentes':'Recent News'}</h3>
                            
                        </div>
                        <Nouvelles nb={4} />
                        <div className='more'>
                            <h4><Link to={lang==='fr'? '/nouvelles':'/en/news'}>{lang==='fr'?'toutes les nouvelles':'All News'}</Link></h4>     
                                <svg style={{transform: 'rotate(-45deg)'}} fill='none' viewBox='0 0 60 60' width="30" height="30">
                                    
                                    <path d='M5,30 L55,30 M40,15 L55,30 L40,45' stroke='black' strokeWidth='2px' stroke-linecap="round"/>
                                    
                                </svg>
                            
                            </div>
                        </div>

                    <div className='portrait'>
                    <img  src='https://cdn.sanity.io/images/r9dm8hy5/production/53a6c217825efd42b1798115ee4521e5a4d76394-5456x3064.jpg?fit=max'></img>
                    </div>

                    
                
        </motion.div>
    )
}

export default Home
